* {
  box-sizing: border-box;
  font-family: 'Dosis', sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.card {
    background: #fff;
    border-radius: 5px;
    height: 150px;
    margin: 0 auto;
    padding: 8px 8px 8px;
    position: relative;
    width: 150px;
    box-shadow: 0 3px 6px #999, 0 3px 6px #999;
 
  }
  
  .card > .img-container {
    height: 80%;
    overflow: hidden;
    text-align: center;
    background: #A5E8D1;
  }

  .card > .img-container > img {
    width: 60%
  }
  
  @media only screen and (max-width: 767px) {
    .card, .img-container {
      width: 100%;
      margin-bottom: 3px; 
    }
  }
nav {
    width: 100%;
    color: #fff;
    margin-bottom: 25px;
    background-color: rgba(56, 56, 56, 0.884);
    height: auto;
    border-radius: 10px;
  }
  
  a {
    font-family: 'ZCOOL QingKe HuangYou', cursive;
    color: #fed420;
  }
  
  a:hover {
    color: #20fece;
    text-decoration: none;
  }
  
  nav li {
    display: inline-block;
    font-size: 1.5rem;
    padding: 15px 0;
    width: 40%;
    font-family: 'ZCOOL QingKe HuangYou', cursive;
  }

  .brand{
    font-size: 1.9rem;
    text-shadow: #FFFCA8 2px 2px 0px, #9C9C9C 1px 2px 0px;
  }
  
  ul {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
  }
  
  @media only screen and (max-width: 750px) {
    #current-score, #top-score, #message, .brand {
      font-size: .7rem;
      margin-top: 10px;
      padding: 0;
    }
}

.jumbotron {
    margin: 0;
    padding: 0;
    color: yellow;
}
.display-3 {
    font-family: 'Bungee Shade', cursive;    
    text-align: center;
    font-size: 2rem;
    color: rgb(6, 139, 117);
    font-weight: 600;
    text-transform: uppercase;
}

.lead {
    text-align: center;
    font-size: 1rem;
    font-family: 'Dosis', sans-serif;
    font-weight: bold;
}
.wrapper {
    padding-top: 50px;
    background-image: -webkit-linear-gradient(left top,#fffd7b, rgb(17, 141, 131));
    background-image: linear-gradient(to right bottom,#fffd7b, rgb(17, 141, 131));
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
